import React from "react";
import NeedyComponent from "./NeedyComponent";
import styles from "./css/loader.module.css";
import HighOrderComponent from "./HighOrderComponent";
import "bootstrap/dist/css/bootstrap.min.css";
import Container from "react-bootstrap/Container";
const Result = HighOrderComponent(NeedyComponent);
class HocCall extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      data: [
        {
          id: 1,
          name: "Kajal",
          city: "Vapi",
          profession: "Developer",
          category: "Staff",
          email: "kajal@gmail.com",
        },
        {
          id: 2,
          name: "Amit",
          city: "Pune",
          profession: "Developer",
          category: "Staff",
          email: "amit@gmail.com",
        },
        {
          id: 3,
          name: "Ankit",
          city: "Kolkata",
          profession: "Teacher",
          category: "Staff",
          email: "ankit@gmail.com",
        },
        {
          id: 4,
          name: "Vishal",
          city: "Ahmedabad",
          profession: "Doctor",
          category: "Staff",
          email: "vishal@gmail.com",
        },
        {
          id: 5,
          name: "Nikita",
          city: "Vadodara",
          profession: "Chemist",
          category: "Staff",
          email: "nikita@gmail.com",
        },
      ],
    };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ isLoading: false });
    }, 3000);
    document.title = "Day-11-HOC";
  }
  render() {
    return (
      <Container className="mx-auto py-5">
        <h1 className={`${styles.Title}`}>Displaying Information</h1>
        <Result data={this.state.data} isLoading={this.state.isLoading} />
      </Container>
    );
  }
}
export default HocCall;
