import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Card from "react-bootstrap/Card";
function DisplayComponent(props) {
  return (
    <Card style={{ width: "18rem" }} className="my-4 border-primary mx-4">
      <Card.Body>
        <Card.Title>User {props.value.id}</Card.Title>
        <Card.Subtitle className="mb-2 text-muted">
          user_id: {props.value.id}
        </Card.Subtitle>
        <Card.Text>
          <b>Name: {props.value.name}</b>
          <br />
          <b>Email: {props.value.email}</b>
          <br />
          <b>Profession: {props.value.profession}</b>
          <br />
          <b>Category: {props.value.category}</b>
          <br />
          <b>City: {props.value.city}</b>
        </Card.Text>
        <Card.Link href={`mailto:${props.value.email}`}>Send Mail</Card.Link>
      </Card.Body>
    </Card>
  );
}
const NeedyComponent = (props) => {
  const { data } = props;
  if (!data) {
    alert("Invalid Request");
  }
  if (data.length === 0) {
    alert("No details available");
  }
  return (
    <div className="row">
      {data.map((user) => (
        <DisplayComponent key={user.id} value={user} />
      ))}
    </div>
  );
};
export default NeedyComponent;
